import React from 'react';
import './App.css';

import Sequencer from './lib/sequencer.js';
import Swiper from 'swiper';

const hasTouch = 'ontouchstart' in window || navigator.msMaxTouchPoints;

const SRC = {
  mgyon: {
    width: 720,
    height: 1080,
    initialFrame: 6590,
    initialHour: 17,
    initialMinute: 16,

    finalFrame: 7009,
    finalHour: 17,
    finalMinute: 23,
    list: generateList(`mgyonp/DSCF`, 6591, 7009, [7000, 6764, 6825, 6732]).filter((e, eIdx) => {
      if (e.photoId < 6590 + 30) return true;
      if (e.photoId > 7009 - 40) return eIdx % 2 === 0;
      return eIdx % 3 === 0;
    })
  },
  thunder: {
    width: 720,
    height: 1080,
    initialFrame: 1887,
    initialHour: 21,
    initialMinute: 0,

    finalFrame: 2439,
    finalHour: 21,
    finalMinute: 55,
    list: generateList(`thunderp/DSCF`, 1888, 2339, []).filter((e, eIdx) => {
      if (eIdx < 30) return true;
      if (eIdx > 130 && eIdx < 190) return true;
      // return eIdx % 3 === 0 || eIdx % 3 === 1;
      return eIdx % 2 === 1;
    })
  },
  drone: {
    width: 1080,
    height: 720,
    initialFrame: 8274,
    initialHour: 17,
    initialMinute: 47,

    finalFrame: 8564,
    finalHour: 17,
    finalMinute: 52,
    list: generateList(`dronep/DSCF`, 8310, 8554, []).filter((e, eIdx) => {
      if (eIdx > 100 || eIdx < 10) return true;
      return eIdx % 2 === 0;
    })
  }
};

const GALLERY = ['drone', 'mgyon', 'thunder'].sort(() => {
  return Math.random() - 0.5;
});

window.INSTANCES = {};
window.CURRENT_INSTANCE = GALLERY[0];

function generateList(path, start, end, exceptions) {
  return new Array(end - start)
    .fill('')
    .map((el, elIdx) => {
      if (exceptions.includes(start + elIdx)) return null;
      return {
        path: `${path}${start + elIdx}.jpg`,
        photoId: start + elIdx
      };
    })
    .filter((e) => !!e);
}

function parseTime(frameTime) {
  const hour = `0${Math.floor(frameTime / 60 / 60) % 24}`.slice(-2);
  const minute = `0${Math.floor(frameTime / 60) % 60}`.slice(-2);
  const second = `0${Math.floor(frameTime % 60)}`.slice(-2);
  const deciSecond = `00${Math.floor((frameTime % 1) * 100)}`.slice(-2);

  return `${hour}:${minute}:${second}.${deciSecond}`;
}

function getTime({ id, list, initialFrame, finalFrame, initialHour, initialMinute, finalHour, finalMinute }) {
  const initialTime = (initialHour * 60 + initialMinute) * 60;
  const finalTime = (finalHour * 60 + finalMinute) * 60;
  const currentPhotoId = list[id].photoId;
  const interPhotoTime = (finalTime - initialTime) / (finalFrame - initialFrame);
  const frameTime = (currentPhotoId - initialFrame) * interPhotoTime + initialTime;
  return parseTime(frameTime);
}

function Slide(props) {
  const [started, setStarted] = React.useState(false);
  const { name, list, height, width, active, onLoad = () => {}, onScreen } = props;
  const [loading, setLoading] = React.useState(true);
  const [sequence, setSequence] = React.useState(null);
  const [load, setLoad] = React.useState([0, 1]);
  const [current, setCurrent] = React.useState(-1);

  React.useEffect(() => {
    if (!active || started) return;

    setStarted(true);

    const options = {
      direction: 'x',
      scaleMode: 'contain',
      showLoadedImages: true,
      playMode: 'hover',
      originalHeight: height,
      originalWidth: width,
      hiDPI: false,
      imageLoad: function (e) {
        setLoad([Math.max(load[0], e.count), e.total]);
      },
      queueComplete: function (e) {
        setLoading(false);
        onLoad();
      },
      onSelect: function (id) {
        setCurrent(id);
      }
    };

    const newSequence = Sequencer.make({
      ...options,
      list: list,
      canvas: document.querySelector(`.swiper-slide.-${name} canvas`)
    });

    window.requestAnimationFrame(() => {
      newSequence.size(null, null, onScreen);
    });
    newSequence.size(null, null, onScreen);

    window.INSTANCES[name] = newSequence;
    setSequence(newSequence);
  }, [active, started]);

  React.useEffect(() => {
    onScreen && sequence && sequence.size(null, null, true);
  }, [onScreen]);

  return (
    <div className={`swiper-slide -${name}`}>
      <div
        className="canvas-shadow"
        style={{
          transform: `translatex(${-100 + (current / list.length) * 100}vw)`
        }}
      />
      <div className="canvas-container">
        <div className="canvas-wrapper">
          <canvas className="canvas" key="canvas" />
        </div>
        {loading && <div className="info">Loading... {Number((load[0] / load[1]) * 100).toFixed(0)}%</div>}
        {!loading && current === -1 && <div className="info">{hasTouch ? '← Scroll →' : '← Hover →'}</div>}
        {!loading && current !== -1 && <div className="info -number">{getTime({ id: current, ...SRC[name] })}</div>}
      </div>
    </div>
  );
}

function App() {
  const [loadedSlide, setLoadedSlide] = React.useState(0);
  const [currentSlide, setCurrentSlide] = React.useState(0);

  React.useEffect(() => {
    function globalResize() {
      document.querySelectorAll('.swiper-slide').forEach((e) => {
        e.style.height = `${window.innerHeight}px`;
      });
      document.querySelectorAll('.swiper-container').forEach((e) => {
        e.style.height = `${window.innerHeight}px`;
      });

      for (const galleryName in window.INSTANCES) {
        window.INSTANCES[galleryName].size();
      }

      const currentCanvas = document.querySelector(`.swiper-slide.-${window.CURRENT_INSTANCE} .canvas-wrapper`);
      if (currentCanvas) {
        const { width, height } = currentCanvas.getBoundingClientRect();

        document.querySelectorAll(`.-note-top`).forEach((e) => {
          e.style.height = `${Math.round((window.innerHeight - height) / 2 - window.PADDING_TOP_DIFF + 2)}px`;
        });
        document.querySelectorAll(`.-note-bottom`).forEach((e) => {
          e.style.height = `${Math.round((window.innerHeight - height) / 2 + window.PADDING_TOP_DIFF * 1.5)}px`;
        });
      }
    }

    window.setTimeout(() => {
      const mySwiper = new Swiper('.swiper-container', {
        direction: 'vertical',
        mousewheel: true
      });

      document.body.classList.add(`-${GALLERY[mySwiper.activeIndex]}`);
      mySwiper.on('slideChange', function () {
        for (const currentClass of document.body.classList) {
          // if the body class is one of the slides
          if (currentClass.includes('-')) {
            document.body.classList.remove(currentClass);
          }
        }

        document.body.classList.add(`-${GALLERY[mySwiper.activeIndex]}`);
        setCurrentSlide(mySwiper.activeIndex);
        window.CURRENT_INSTANCE = GALLERY[mySwiper.activeIndex];
        window.setTimeout(globalResize, 8);
      });

      globalResize();
    }, 8);

    globalResize();
    window.addEventListener('resize', globalResize);
  }, []);

  React.useEffect(() => {
    setLoadedSlide(Math.max(loadedSlide, currentSlide));
  }, [loadedSlide, currentSlide]);

  document.title = window.CURRENT_INSTANCE;

  return (
    <div className="swiper-container">
      <div className="swiper-wrapper">
        {GALLERY.map((timelapseName, timelapseIdx) => {
          return (
            <Slide
              key={timelapseName}
              active={loadedSlide >= timelapseIdx}
              onLoad={() => {
                setLoadedSlide(Math.max(loadedSlide, timelapseIdx + 1));
              }}
              height={SRC[timelapseName].height}
              width={SRC[timelapseName].width}
              list={SRC[timelapseName].list.map((e) => e.path)}
              onScreen={currentSlide === 0}
              name={timelapseName}
            />
          );
        })}
      </div>

      {hasTouch ? (
        <div className="note -note-top -how">Scroll the picture ⤵ to change time.</div>
      ) : (
        <div className="note -note-top -how">Hover over the picture ⤵ to change time.</div>
      )}

      {currentSlide < GALLERY.length - 1 && <div className="note -note-bottom -more">There is more ↓</div>}
      {currentSlide === GALLERY.length - 1 && (
        <div className="note -note-bottom -more">
          <div>
            Photos and web by <a href="https://javier.xyz">javierbyte</a>
          </div>
          <div>Thanks! v0.15</div>
        </div>
      )}
    </div>
  );
}

export default App;
